div.foo{
    width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  .letter{
    display: inline-block;
    font-weight: 900;
    font-size: 1.7em;
    margin: 0.2em;
    position: relative;
    color: #00B4F1;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-perspective: 400;
            perspective: 400;
    z-index: 1;
  }
  .letter:before, .letter:after{
    position:absolute;
    content: attr(data-letter);
    -webkit-transform-origin: top left;
            transform-origin: top left;
    top:0;
    left:0;
  }
  .letter, .letter:before, .letter:after{
    transition: all 0.3s ease-in-out;
  }
  .letter:before{
    color: #fff;
    text-shadow: 
      -1px 0px 1px rgba(255,255,255,.8),
      3px 0px 1px rgba(0,0,0,.8);
    z-index: 3;
    -webkit-transform:
      rotateX(0deg)
      rotateY(-45deg)
      rotateZ(0deg);
            transform:
      rotateX(0deg)
      rotateY(-45deg)
      rotateZ(0deg);
  }
  .letter:after{
    color: rgba(0,0,0,.11);
    z-index:2;
    -webkit-transform:
      scale(1.08,1)
      rotateX(0deg)
      rotateY(0deg)
      rotateZ(0deg)
      skew(0deg,1deg);
            transform:
      scale(1.08,1)
      rotateX(0deg)
      rotateY(0deg)
      rotateZ(0deg)
      skew(0deg,1deg);
  }
  .letter:hover:before{
    color: #fafafa;
    -webkit-transform:
      rotateX(0deg)
      rotateY(-90deg)
      rotateZ(0deg);
            transform:
      rotateX(0deg)
      rotateY(-90deg)
      rotateZ(0deg);
  }
  .letter:hover:after{
    -webkit-transform:
      scale(1.08,1)
      rotateX(0deg)
      rotateY(40deg)
      rotateZ(0deg)
      skew(0deg,22deg);
            transform:
      scale(1.08,1)
      rotateX(0deg)
      rotateY(40deg)
      rotateZ(0deg)
      skew(0deg,22deg);
  }

  .foo{
    flex-wrap:wrap;
  }

  .your-name, .your, .rank, .is{
    padding-right:10px;
  }
.number{
  display:block!important;
}

/* MEDIA QUERIES */

@media screen and (min-width:320px){
